import React, {Component} from 'react';
import ImageInteractive from './ImageInteractive';

let prob_data;

class App extends Component {
   constructor() {
      super();
      this.state = {data : null};
   }
   compareFunc(a, b) {
      if (a.order > b.order) return 1;
      if (b.order > a.order) return -1;
      return 0;
   }
   componentDidMount() {
     prob_data = {
         name : 'One-Step Equations',
         media : 'images-interactive',
         audio_abbrev : 'oseq_narration',
         steps : []
     };
     let errors = [
         {'default' : 'Rewrite the number as an expression using the operation indicated.', 'non-fraction' : 'Read the directions. You need to write a fraction here.', '12-as-numerator' : '12 over 2 is equal to 6. What over 2 is equal to 12?', '25-as-minuend' : '25 minus 5 is equal to 20. What minus 5 is equal to 25?', '16-as-addend' : '16 plus 4 is equal to 20. What plus 4 is equal to 16?'},
         {'division' : 'You are already dividing on both sides. What can you do to "undo" dividing?', 'default' : 'Look at the operation being applied to both sides. What is the inverse operation of that operation?', 'subtraction' : 'You are already subtracting on both sides. What can you do to "undo" subtracting?', 'addition' : 'You are already adding on both sides. What can you do to "undo" adding?'},
         {'default' : 'The resulting equation should be of the form m = ?'},
         {'default' : 'The resulting equation should be of the form m = ?', 'wrong-form' : 'Write the solution in the form m = ? or ? = m.'},
         {'default' : 'Try to match the form of the original equation exactly, except for replacing the variable with the solution.'}
     ];
     let sorted_data = [];
     fetch('https://api.airtable.com/v0/appbJvIOkBMSrEz8z/One-Step%20Equations?api_key=keyuFtFTBZMHrzjOn')
     .then((resp) => resp.json())
     .then(data => {
      for (var a = 0; a < data.records.length; a++) {
      data.records[a].fields.errors = errors[data.records[a].fields.order - 1];
      if (data.records[a].fields.step === 'reflectQ') prob_data.reflectQ = data.records[a].fields.text;
      else if (data.records[a].fields.step === 'reflect_suganswer') prob_data.reflect_suganswer = data.records[a].fields.text;
      else if (data.records[a].fields.step === 'testimgs') prob_data.testimgs = data.records[a].fields.Attachments
      else sorted_data.push(data.records[a].fields);
      }
      sorted_data = sorted_data.sort(this.compareFunc);
      sorted_data.forEach(function(record){prob_data.steps.push(record);});
      this.setState({data : prob_data});
     })
     .catch(err => {});
   }
   render() {
      if (!this.state.data || this.state.data === undefined || this.state.data === null) return null;
      let primary = '#4499cc';
      let secondary = '#557722';
      const disabled = '#D3D3D3';
      let dislabel = '#aaccdd';

      let scala;
      if (this.props.media === 'images-interactive') {
         scala = <ImageInteractive primcolor={primary} seccolor={secondary} discolor={disabled} dislabel={dislabel} probdata={this.state.data} />
      }
      return (
      <div>
      {scala}
      </div>
      );
   }
}

export default App;
