import React, {Component} from 'react';
import $ from 'jquery';

class StepCard extends Component {
   constructor() {
      super();
      this.state = {};
      this.openError = this.openError.bind(this);
   }
   componentDidMount() {
      $(`#steptext${this.props.order}`).html(this.props.text);
      $('#error-message').html(this.props.probdata.steps[this.props.curbut - 1].errors['default']);
   }
   openError(evt) {
      window.UIkit.modal('#modal-error').show();
      evt.stopPropagation();
   }
   render() {
      let cardStyle, labelStyle, arrowStyle, textStyle, icon, repicon, repStyle, qmarkStyle;
      if (this.props.curmode === 'learn') {
        qmarkStyle = {display : 'none'};
        icon = '';
        repicon = '';
        if (this.props.order < this.props.curbut) {
        cardStyle = {pointerEvents : 'none', backgroundColor : 'white', color : 'black'};
        labelStyle = {backgroundColor : this.props.primcolor};
        }
        else if (this.props.order === this.props.curbut) {
        cardStyle = {pointerEvents : 'all', backgroundColor : this.props.discolor, color : 'gray'};
        labelStyle = {backgroundColor : this.props.dislabel};
        textStyle = {filter : 'blur(3px)'};
        }
        else {
        cardStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor, color : 'gray'};
        labelStyle = {backgroundColor : this.props.dislabel};
        textStyle = {filter : 'blur(3px)'};
        }
        if (this.props.order === this.props.curbut-1 && this.props.curbut > 1) {
           arrowStyle = repStyle = {visibility : 'visible', cursor : 'pointer', pointerEvents : 'all'}
        }
        else {
           arrowStyle = repStyle = {visibility : 'hidden', cursor : 'none', pointerEvents : 'none'}
        }
      }
      else {
        arrowStyle = {visibility : 'visible', cursor : 'none', pointerEvents : 'none'};
        repStyle = {visibility : 'hidden', cursor : 'none', pointerEvents : 'none'};
        qmarkStyle = {visibility : 'visible', cursor : 'pointer', pointerEvents : 'all'};
        if (this.props.order > this.props.curmark) {
        qmarkStyle = {visibility : 'hidden', cursor : 'none', pointerEvents : 'none'};
        cardStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor, color : 'gray'};
        labelStyle = {backgroundColor : this.props.dislabel};
        textStyle = {filter : 'blur(3px)'};
        icon = '';
        }
        else if (this.props.order === this.props.curmark) {
        if (this.props.curerror) $('.qmark').addClass('uk-animation-shake');
        cardStyle = {pointerEvents : 'none', backgroundColor : 'white', color : 'black'};
        labelStyle = {backgroundColor : this.props.primcolor};
        icon = '';
        }
        else {
        qmarkStyle = {visibility : 'hidden', cursor : 'none', pointerEvents : 'none'};
        cardStyle = {pointerEvents : 'none', backgroundColor : 'rgba(0, 255, 0, 0.1)'};
        labelStyle = {backgroundColor : this.props.primcolor};
        icon = '';
        }
      }
      return (
      <div className={`uk-card uk-card-default uk-card-body uk-card-small uk-width-1-1@m ${this.props.margBot}`} id={`card${this.props.order}`} onClick={this.props.handler} style={cardStyle}>
      <div><span className='qmark' onClick={this.openError} style={qmarkStyle}>?</span><span className='arrow' onClick={this.props.upone} style={arrowStyle}>{icon}</span><span className='reparrow' onClick={this.props.repstep} style={repStyle}>{repicon}</span><span className="uk-label" style={labelStyle}>{this.props.label}</span> <p className='step-text' id={`steptext${this.props.order}`} style={textStyle}>{this.props.text}</p></div>
      </div>
      );
   }
}

export default StepCard;
