import React, {Component} from 'react';
import StepCard from './StepCard';
import Reflect from './Reflect';
import InteractiveSVG from './InteractiveSVG';
import $ from 'jquery';

class ImageInteractive extends Component {
   constructor() {
      super();
      this.state = {
      currentButton : 1, mode : 'test', currentAnswerMark : 1, mediaDimTag : '',
      testKey : 1, audioVol : 1, userTested : false, PNUM : 3, curerror : false
      };
      this.childClick = this.childClick.bind(this);
      this.startOver = this.startOver.bind(this);
      this.reset = this.reset.bind(this);
      this.upOne = this.upOne.bind(this);
      this.testMode = this.testMode.bind(this);
      this.learnMode = this.learnMode.bind(this);
      this.correctAnswer = this.correctAnswer.bind(this);
      this.updateTest = this.updateTest.bind(this);
      this.setAudio = this.setAudio.bind(this);
      this.repeatStep = this.repeatStep.bind(this);
      this.userTested = this.userTested.bind(this);
      this.updateState = this.updateState.bind(this);
      this.resetError = this.resetError.bind(this);
      this.incorrectAnswer = this.incorrectAnswer.bind(this);
   }
   userTested(res) {
      if (res) this.setState({userTested : true});
   }
   updateMedia() {
     let wdt = $('#media-container').width();
     let hgt = $('#media-container').height();
     let mediatag;
     if (wdt / hgt <= 1.2) mediatag = '_square';
     else mediatag = '';
     this.setState({mediaDimTag : mediatag});
     if (wdt / hgt > 1.72) this.setState({mediaDims : {height : '65vh'}});
     else this.setState({mediaDims : {height : '54vh'}});
   }
   componentDidMount(props) {
      this.updateMedia();
      let wdt = $('#media-container').width();
      let hgt = $('#media-container').height();
      if (wdt / hgt > 1.72) this.setState({mediaDims : {height : '65vh'}});
      window.addEventListener('resize', this.updateMedia.bind(this));
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 100);
   }
   childClick() {
      this.setState({currentButton : this.state.currentButton + 1}, function() {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
      setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 100);
      });
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${this.props.probdata.audio_abbrev}${this.state.currentButton}`);
      if (audio) {
         audio.volume = this.state.audioVol;
         audio.src = this.props.probdata.steps[this.state.currentButton - 1].Attachments[2].url;
         audio.play();
      }
   }
   resetError() {
      this.setState({curerror : false});
      $('.qmark').removeClass('uk-animation-shake');
      let deflt = this.props.probdata.steps[this.state.testKey - 1].errors['default'];
      $('#error-message').html(deflt);
   }
   incorrectAnswer(errcode) {
      let errorMessage;
      if (!errcode) errorMessage = this.props.probdata.steps[this.state.currentAnswerMark - 1].errors['default'];
      else {
      errorMessage = this.props.probdata.steps[this.state.currentAnswerMark - 1].errors[errcode];
      this.setState({curerror : true});
      }
      $('#error-message').html(errorMessage);
   }
   correctAnswer(fin) {
      this.setState({currentAnswerMark : this.state.currentAnswerMark + 1, curerror : false}, function() {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      if (!fin) {
         const cardLoc = document.getElementById(`card${this.state.currentAnswerMark}`).offsetTop;
         setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 800);
      }
      });
   }
   startOver() {
      this.setState({currentButton : 2}, function() {
         let sounds = document.getElementsByTagName('audio');
         for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
         let audio = document.getElementById(`${this.props.probdata.audio_abbrev}${this.state.currentButton - 1}`);
         if (audio) {
         audio.volume = this.state.audioVol;
         audio.play();
         }
         }
      );
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
   }
   reset() {
      this.setState({currentButton : 1}, function() {
         let sounds = document.getElementsByTagName('audio');
         for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
         }
      );
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
   }
   upOne(evt) {
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${this.props.probdata.audio_abbrev}${this.state.currentButton - 2}`);
      if (audio) {
         audio.volume = this.state.audioVol;
         audio.play();
      }
      evt.stopPropagation();
      this.setState({currentButton : this.state.currentButton - 1}, function() {
      if (this.state.currentButton > 1) {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
      setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 100);
      }
      });
   }
   repeatStep(evt) {
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${this.props.probdata.audio_abbrev}${this.state.currentButton - 1}`);
      if (audio) {
         audio.volume = this.state.audioVol;
         audio.play();
      }
      evt.stopPropagation();
   }
   setAudio() {
      let level = this.state.audioVol;
      if (level === 0) level = 1;
      else level = 0;
      this.setState({audioVol : level});
      let audio = document.getElementById(`${this.props.probdata.audio_abbrev}${this.state.currentButton - 1}`);
      if (audio) audio.volume = level;
   }
   testMode() {
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      let sound = document.getElementById(`${this.props.probdata.audio_abbrev}${this.state.currentButton - 1}`);
      if (sound) {sound.pause(); sound.currentTime = 0;}
      setTimeout(function(){$('#error-message').html(this.props.probdata.steps[this.state.currentAnswerMark - 1].errors['default']);}.bind(this), 500);
   }
   updateTest() {
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1,
                     testKey : this.state.testKey + 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#anssub1').text('');
      $('#anssub2').text('');
      $('#anssub3').text('');
   }
   learnMode() {
      this.setState({currentButton : 1, mode : 'learn', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
   }
   updateState(num, ans) {
      this.setState({testKey : num, currentAnswerMark : ans, mode : 'test'});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      if (ans <= 6) {
      const cardLoc = document.getElementById(`card${ans}`).offsetTop;
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      setTimeout(function(){
      stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });
      }, 800);
      }
   }
   render() {
      if (!this.props.probdata || this.props.probdata === undefined || this.props.probdata == null) return null;
      let modeMedia, butStyle, buttonChoices, mediaStyle, testText, reflect, audvis, margBot;
      let stepCards = [];
      mediaStyle = this.state.mediaDims;

      if (this.state.currentButton <= this.props.probdata.steps.length) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};

      if (this.state.mode === 'learn') {
      for (let a = 0; a < this.props.probdata.steps.length; a++) {
         if (a !== this.props.probdata.steps.length - 1) margBot = 'uk-margin-small-bottom';
         else margBot = 'uk-margin-large-bottom';
         stepCards.push(<StepCard key={this.props.probdata.steps[a].id} label={this.props.probdata.steps[a].label} text={this.props.probdata.steps[a].text} order={this.props.probdata.steps[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} upone={this.upOne} repstep={this.repeatStep} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} margBot={margBot} probdata={this.props.probdata} />)
      }

      buttonChoices = (
         <span>
         <button onClick={this.startOver} className='uk-button uk-button-default uk-button-small uk-width-1-3' style={butStyle}>redo</button>

         <button style={{backgroundColor : this.props.seccolor}} onClick={this.reset} className='uk-button uk-button-secondary uk-button-small uk-width-1-3'>clear</button>

         <button style={{backgroundColor : this.props.primcolor}} onClick={this.testMode} className='uk-button uk-button-primary uk-button-small uk-width-1-3'>show</button>
         </span>
      );
      let attachnum;
      if (this.state.mediaDimTag === '_square') attachnum = 1;
      else attachnum = 0;
      modeMedia = <img src={this.state.currentButton !== 1 ? this.props.probdata.steps[this.state.currentButton - 2].Attachments[attachnum].url : ''} alt='' />;
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
         audvis = 'hidden';
      }
      else audvis = 'visible';
      reflect = <Reflect suganswer={this.props.probdata.reflect_suganswer} reflectQ={this.props.probdata.reflectQ} />;
      }
      else  {
      if (this.state.currentButton <= this.props.probdata.steps.length) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};

      buttonChoices = (
         <span>
         <button style={{backgroundColor : this.props.seccolor}} id='updateTest' onClick={this.updateTest} className='uk-button uk-button-secondary uk-button-small uk-width-1-2'>another one</button>

         <button style={{backgroundColor : this.props.primcolor}} onClick={this.learnMode} className='uk-button uk-button-primary uk-button-small uk-width-1-2'>learn&thinsp;<span uk-icon='social'></span></button>
         </span>
      );
      for (let a = 0; a < this.props.probdata.steps.length; a++) {
         if (a !== this.props.probdata.steps.length - 1) margBot = 'uk-margin-small-bottom';
         else margBot = 'uk-margin-large-bottom';
         if (this.state.testKey % this.state.PNUM === 1) testText = this.props.probdata.steps[a].test;
         else if (this.state.testKey % this.state.PNUM===0) testText = this.props.probdata.steps[a][`test${this.state.PNUM}`];
         else testText = this.props.probdata.steps[a][`test${this.state.testKey % this.state.PNUM}`];

         stepCards.push(<StepCard key={this.props.probdata.steps[a].id} label={this.props.probdata.steps[a].testLabel} text={testText} order={this.props.probdata.steps[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} curmark={this.state.currentAnswerMark} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} margBot={margBot} curerror={this.state.curerror} probdata={this.props.probdata} />);
      }

      modeMedia = <InteractiveSVG testnum={this.state.testKey} curbut={this.state.currentButton} correctans={this.correctAnswer} curans={this.state.currentAnswerMark} tested={this.userTested} pnum={this.state.PNUM} updatestate = {this.updateState} reseterror={this.resetError} incorrectans={this.incorrectAnswer} testimgs={this.props.probdata.testimgs} />;

      reflect = null;
      audvis = 'hidden';
      }
      return (
      <div>
      <div id='media-container' className='uk-container uk-width-1-2@m uk-border-rounded' style={mediaStyle}>
      {modeMedia}
      <img onClick={this.setAudio} id='soundicon' src='audio_icon.svg' alt='' style={{opacity : ((1 - this.state.audioVol) * 1.5) / 1.5 + 0.2, visibility : audvis}}/>
      </div>

      <div id='steps-container' className='uk-container uk-width-1-2@m'>
      {stepCards}
      {reflect}
      {buttonChoices}
      </div>
      </div>
      );
   }
}

export default ImageInteractive;
